import React, {useState} from 'react';
import './jobs.css'
import axios from "axios";
function AddJobs() {

    const[job,setJob]=useState("")
    const[department,setDepartment]=useState("")
    const[salaryfrom,setSalaryFrom]=useState("")
    const[salaryTo,setSalaryTo]=useState("")
    const[experience,setExperience]=useState("")
    const[skill,setSkills]=useState("")
    const[qualification,setQualification]=useState("")
    const[smalldesc,setSmallDescr]=useState("")
    const[bigdesc,setBigDesc]=useState("")
    const[comapny,setCompany]=useState("")
    const[email,setEmail]=useState("")
    const[phone,setPhone]=useState("")
    const[companydesc,setCompanyDesc]=useState("")


    function sendData(event){
    
        event.preventDefault();
        axios.post('https://futurecare.herokuapp.com/addJobs',{job:job,department:department,salaryfrom:salaryfrom,salaryTo:salaryTo,experience:experience,skill:skill,qualification:qualification,
        smalldesc:smalldesc,bigdesc:bigdesc,comapny:comapny,email:email,phone:phone,companydesc:companydesc})
        .then((res) =>console.log("succefully"))
        .catch(error => {
          alert("not send");
            console.error('There was an error!', error);
        }); 
    };

    return(
      <div className="form">
        <h1 className='heading'>Add New Jobs</h1>
          <div className="form-body">
              <div className="username">
                  <label className="form__label" for="firstName">Job Title </label>
                  <input className="form__input" type="text" id="firstName" placeholder="Job Title"  onChange={(e)=>{setJob(e.target.value)}}/>
              </div>
              <div className="lastname">
                  <label className="form__label" for="lastName">Department</label>
                  <input  type="text" name="" id="department"  className="form__input"placeholder="Job Title"  onChange={(e)=>{setDepartment(e.target.value)}}/>
              </div>
              <div className="email">
                  <label className="form__label" for="email">Salary From </label>
                  <input  type="text" id="salaryfrom" className="form__input" placeholder="Salary From"  onChange={(e)=>{setSalaryFrom(e.target.value)}}/>
              </div>
              <div className="password">
                  <label className="form__label" for="password">Salary To </label>
                  <input className="form__input" type="text"  id="salaryto" placeholder="Salary To"  onChange={(e)=>{setSalaryTo(e.target.value)}}/>
              </div>
              <div className="confirm-password">
                  <label className="form__label" for="confirmPassword">Experience </label>
                  <input className="form__input" type="text" id="experience" placeholder="Experience"  onChange={(e)=>{setExperience(e.target.value)}}/>
              </div>

              <div className="confirm-password">
                  <label className="form__label" for="confirmPassword">Skills Required </label>
                  <input className="form__input" type="text" id="skillsrequired" placeholder="Skills Required"  onChange={(e)=>{setSkills(e.target.value)}}/>
              </div>


              <div className="confirm-password">
                  <label className="form__label" for="confirmPassword">Minimum Qualification </label>
                  <input className="form__input" type="text" id="minimumqualification" placeholder="Minimum Qualification "  onChange={(e)=>{setQualification(e.target.value)}}/>
              </div>

              <div className="confirm-password">
                  <label className="form__label" for="confirmPassword">Small Description </label>
                  <textarea className="form__input" type="text" id="smalldescription" placeholder="Small Description"  onChange={(e)=>{setSmallDescr(e.target.value)}}/>
              </div>

              <div className="confirm-password">
                  <label className="form__label" for="confirmPassword">Full Description </label>
                  <textarea className="form__input" type="text" id="fulldescription" placeholder="Full Description"  onChange={(e)=>{setBigDesc(e.target.value)}}/>
              </div>

              <div className="confirm-password">
                  <label className="form__label" for="confirmPassword">Company </label>
                  <input className="form__input" type="text" id="comapny" placeholder="Company"  onChange={(e)=>{setCompany(e.target.value)}}/>
              </div>

              <div className="confirm-password">
                  <label className="form__label" for="confirmPassword">E-Mail </label>
                  <input className="form__input" type="text" id="email" placeholder="E-mail"  onChange={(e)=>{setEmail(e.target.value)}}/>
              </div>

     

              <div className="confirm-password">
                  <label className="form__label" for="confirmPassword">Phone </label>
                  <input className="form__input" type="text" id="phone" placeholder="Phone"  onChange={(e)=>{setPhone(e.target.value)}}/>
              </div>

              <div className="confirm-password">
                  <label className="form__label" for="confirmPassword">Company Description </label>
                  <input className="form__input" type="text" id="companydescription" placeholder="Company Description"  onChange={(e)=>{setCompanyDesc(e.target.value)}}/>
              </div>




          </div>
          <div class="footer">
              <button type="submit" className='btn' onClick={sendData}>Save</button>
          </div>
      </div>      
    )       
}
export default AddJobs;

